import {
  BrowserRouter as Router,
  Switch,
  Route,
  NavLink
} from "react-router-dom";
import logo from './logo.svg';
import './App.css';
import Status from './components/Status';
import 'antd/dist/antd.css';
import Project from "./components/Project";

function App() {
  const checkActive = (match, location) => {
    //some additional logic to verify you are in the home URI
    if (!location) return false;
    const { pathname } = location;
    console.log(pathname);
    return pathname === "/";
  }

  return (
    <div className="App">
      {/* <header className="App-header">
        
      </header> */}
      <Router>
        <div className='container'>
          <nav>
            <div className="nav-wrapper">
              <ul className='navigation'>
                <li>
                  <NavLink activeStyle={{ color: "red" }} isActive={checkActive} to="/">Home</NavLink>
                </li>
                <li>
                  <NavLink activeStyle={{ color: "red" }} to="/project">Projecten</NavLink>
                </li>
                <li>
                  <NavLink activeStyle={{ color: "red" }} to="/project-status">Statussen</NavLink>
                </li>
              </ul>
            </div>
          </nav>

          {/* A <Switch> looks through its children <Route>s and
            renders the first one that matches the current URL. */}
          <Switch>
            <Route path="/project">
              <Project />
            </Route>
            <Route path="/project-status">
              <Status />
            </Route>
            <Route path="/">
              <p className='welcome-text'>Welkom bij de Broporte admin demo, selecteer projecten of statussen om verder te gaan.</p>
            </Route>
          </Switch>
        </div>
      </Router>
    </div>
  );
}

export default App;
