import { Input, Space } from 'antd';

const CreateStatus = ({submitHandler}) => {
  const { Search } = Input;

  const onSubmit = (value) => {
    const formdata = new FormData();
    formdata.append("value", value);
    fetch('https://broporte-backend.herokuapp.com//api/project-status', { method: 'POST', body: formdata })
      .then(res => res.json())
      .then(json => {
        console.log(json, 'post json');
        submitHandler();
      })
  };

  const onSearch = value => console.log(value);

  return (
    <Space direction="vertical" className='create'>
      <Search
        placeholder="status"
        allowClear
        enterButton="Status toevoegen"
        size="large"
        onSearch={onSubmit}
      />
    </Space>
  );
};

export default CreateStatus;