import React from 'react';
import { Button } from 'antd';
import { DeleteOutlined } from '@ant-design/icons';

const DeleteProject = ({ project, submitHandler }) => {
  const deleteProject = () => {
    fetch(`https://broporte-backend.herokuapp.com//api/project/${project.id}`, { method: 'DELETE' })
      .then(res => res.json())
      .then(json => {
        submitHandler();
      })
  };


  return (
    <>
      <Button type="primary" onClick={deleteProject} danger>
        <DeleteOutlined />
      </Button>
    </>
  );
};

export default DeleteProject;