import React, { useEffect, useState } from 'react'
// import Button from '@material-ui/core/Button';
// import List from '@material-ui/core/List';
// import ListItem from '@material-ui/core/ListItem';
// import ListItemIcon from '@material-ui/core/ListItemIcon';
// import ListItemText from '@material-ui/core/ListItemText';
// import TextField from '@material-ui/core/TextField';
// import AssignmentIcon from '@material-ui/icons/Assignment';
import { List, Avatar, Skeleton } from 'antd';
import CreateStatus from './CreateStatus';
import EditStatus from './EditStatus';
import DeleteStatus from './DeleteStatus';

const Status = () => {
  const [data, setData] = useState();
  const [loading, setLoading] = useState(true);

  const fetchURL = "https://broporte-backend.herokuapp.com//api/project-status";
  const getData = () =>
    fetch(`${fetchURL}`)
      .then((res) => res.json())

  const fetchData = () => {
    getData().then((apiData) => {
      setData(apiData);
      console.log(apiData, '111');
      setLoading(false);
    });
  };

  useEffect(() => {
    fetchData();
  }, []);

  return (
    <div className="main">
      <p className='welcome-text'>Voeg een status toe of bewerk/verwijder een bestaande status.</p>

      <CreateStatus submitHandler={fetchData} />
      <List
        className="list"
        itemLayout="horizontal"
        loading={loading}
        dataSource={data}
        renderItem={item => (
          <List.Item
            actions={[<EditStatus status={item} submitHandler={fetchData} />, <DeleteStatus status={item} submitHandler={fetchData} />]}
          >
            <Skeleton avatar title={false} loading={item.loading} active>
              <List.Item.Meta
                avatar={
                  <Avatar src="data:image/png;base64," />
                }
                title={<a>{item.value}</a>}
              // description="Ant Design, a design language for background applications, is refined by Ant UED Team"
              />
            </Skeleton>
          </List.Item>
        )}
      />
    </div>
  )
}

export default Status;