import React, { useState } from 'react';
import {  Modal, Button, Input  } from 'antd';
import { EditOutlined } from '@ant-design/icons';

const EditStatus = ({ status, submitHandler }) => {
  const [isModalVisible, setIsModalVisible] = useState(false);
  const [statusValue, setStatusValue] = useState();

  const showModal = () => {
    setIsModalVisible(true);
  };

  const handleOk = () => {
    const formdata = new URLSearchParams({
      'value': statusValue,
    });
    fetch(`https://broporte-backend.herokuapp.com//api/project-status/${status.id}`, { method: 'PUT', body: formdata })
    .then(res => res.json())
    .then(json => {
      console.log(json, 'post json');
      submitHandler();
    })
    setIsModalVisible(false);
  };

  const handleCancel = () => {
    setIsModalVisible(false);
  };

  return (
    <>
      <Button type="primary" onClick={showModal}>
        <EditOutlined />
      </Button>
      <Modal title="Basic Modal" visible={isModalVisible} onOk={handleOk} onCancel={handleCancel}>
      <p>Pas de naam van de status aan:</p>
      <Input placeholder={status.value} defaultValue={status.value} onChange={(e) => setStatusValue(e.target.value)} />
      </Modal>
    </>
  );
};

export default EditStatus;