import { useState, useEffect } from 'react';
import { Modal, Button, Input, Select } from 'antd';
import { PlusOutlined } from '@ant-design/icons';

const { Option } = Select;

const CreateProject = ({ submitHandler, statuses }) => {
  const [isModalVisible, setIsModalVisible] = useState(false);
  const [projectNumberValue, setProjectNumberValue] = useState();
  const [clientValue, setClientValue] = useState();
  const [locationValue, setLocationValue] = useState();
  const [statusValue, setStatusValue] = useState();
  const [windowFramesValue, setWindowFramesValue] = useState();
  const [doorsValue, setDoorsValue] = useState();

  // const onSubmit = (value) => {
  //   const formdata = new FormData();
  //   formdata.append("value", value);
  //   fetch('https://broporte-backend.herokuapp.com//api/project', { method: 'POST', body: formdata })
  //     .then(res => res.json())
  //     .then(json => {
  //       console.log(json, 'post json');
  //       submitHandler();
  //     })
  // };

  const showModal = () => {
    setIsModalVisible(true);
  };


  const handleOk = () => {
    const formdata = new URLSearchParams({
      'project_number': projectNumberValue,
      'client': clientValue,
      'location': locationValue,
      'project_status_id': statusValue,
      'window_frames': windowFramesValue,
      'doors': doorsValue,
    });
    fetch(`https://broporte-backend.herokuapp.com//api/project`, { method: 'POST', body: formdata })
      .then(res => res.json())
      .then(json => {
        console.log(json, 'post json');
        if (submitHandler) submitHandler();
      })
    setIsModalVisible(false);
  };

  const handleCancel = () => {
    setIsModalVisible(false);
  };

  return (
    <>
      <Button style={{ marginTop: '25px' }} type="primary" onClick={showModal}>
        <PlusOutlined />
      </Button>
      <Modal title="Basic Modal" visible={isModalVisible} onOk={handleOk} onCancel={handleCancel}>
        <p>Vul de projectgegevens in:</p>
        <label for='project-number'>Projectnummer:</label>
        <Input name='project-number' placeholder='12345' onChange={(e) => setProjectNumberValue(e.target.value)} />

        <label for='client'>Klant:</label>
        <Input name='client' placeholder='Broporte' onChange={(e) => setClientValue(e.target.value)} />

        <label for='client'>Locatie:</label>
        <Input name='client' placeholder='Uden' onChange={(e) => setLocationValue(e.target.value)} />

        <label for='project-status'>Status:</label>
        <div className="">
          <Select style={{ width: 120 }} onChange={(value) => setStatusValue(value)}>
            {statuses.map((status) =>
              <Option value={status.id}>{status.value}</Option>
            )}
          </Select>
        </div>

        <label for='client'>Kozijnen:</label>
        <Input name='client' placeholder='2' onChange={(e) => setWindowFramesValue(e.target.value)} />

        <label for='doors'>Deuren:</label>
        <Input name='doors' placeholder='5' onChange={(e) => setDoorsValue(e.target.value)} />

      </Modal>
    </>
  );
};

export default CreateProject;