import { useEffect, useState } from 'react';
import { Table, Tag, Space } from 'antd';
import CreateProject from './CreateProject';
import DeleteProject from './DeleteProject';
import EditProject from './EditProject';

const Project = () => {
  const [data, setData] = useState();
  const [statuses, setStatuses] = useState();

  const [loading, setLoading] = useState(true);

  const getProjects = () =>
    fetch('https://broporte-backend.herokuapp.com//api/project')
      .then((res) => res.json())

  const getProjectStatuses = () =>
    fetch('https://broporte-backend.herokuapp.com//api/project-status')
      .then((res) => res.json())

  const fetchData = () => {
    getProjects().then((apiData) => {
      setData(apiData);
      console.log(apiData, '111');
    });

    getProjectStatuses().then((apiData) => {
      setStatuses(apiData);
      console.log(apiData, '222');
      setLoading(false);
    });
  };

  useEffect(() => {
    fetchData();
  }, []);

  const columns = [
    {
      title: 'Projectnummer',
      dataIndex: 'project_number',
      key: 'project_number',
      render: text => <a href='https://www.broporte.com/en/' target='_blank'>{text}</a>,
    },
    {
      title: 'Klant',
      dataIndex: 'client',
      key: 'client',
    },
    {
      title: 'Locatie',
      dataIndex: 'location',
      key: 'location',
    },
    {
      title: 'Status',
      dataIndex: 'project_status',
      key: 'project_status',
      render: project_status => project_status.value,
    },
    {
      title: 'Kozijnen',
      dataIndex: 'window_frames',
      key: 'window_frames',
    },
    {
      title: 'Deuren',
      dataIndex: 'doors',
      key: 'doors',
    },
    {
      title: 'Action',
      key: 'action',
      render: (text, record) => (
        <Space size="middle">
          <EditProject project={record} statuses={statuses} submitHandler={fetchData} />
          <DeleteProject project={record} submitHandler={fetchData} />
        </Space>
      ),
    },
  ];

  const renderMain = () => {
    if (loading) return (<p>Loading...</p>);

    return (
      <>
        <CreateProject statuses={statuses} submitHandler={fetchData} />
        <Table style={{ marginTop: '25px' }} columns={columns} dataSource={data} />
      </>
    );
  }

  return (
    <div className='main'>
      <p className='welcome-text'>Voeg een project toe of bewerk/verwijder een bestaand project.</p>
      {renderMain()}
    </div>
  );
}

export default Project;
