import React, { useState, useEffect } from 'react';
import { Modal, Button, Input, Select } from 'antd';
import { EditOutlined } from '@ant-design/icons';

const { Option } = Select;

const EditProject = ({ project, statuses, submitHandler }) => {
  const [isModalVisible, setIsModalVisible] = useState(false);
  const [clientValue, setClientValue] = useState(project.client);
  const [locationValue, setLocationValue] = useState(project.location);
  const [statusValue, setStatusValue] = useState(project.project_status_id);
  const [windowFramesValue, setWindowFramesValue] = useState(project.window_frames);
  const [doorsValue, setDoorsValue] = useState(project.doors);

  const showModal = () => {
    setIsModalVisible(true);
  };

  const handleOk = () => {
    const formdata = new URLSearchParams({
      'client': clientValue,
      'location': locationValue,
      'project_status_id': statusValue,
      'window_frames': windowFramesValue,
      'doors': doorsValue,
    });
    fetch(`https://broporte-backend.herokuapp.com//api/project/${project.id}`, { method: 'PUT', body: formdata })
    .then(res => res.json())
    .then(json => {
      console.log(json, 'post json');
      submitHandler();
    })
    setIsModalVisible(false);
  };

  const handleCancel = () => {
    setIsModalVisible(false);
  };

  useEffect(() => {
    console.log(project);
  }, [project]);

  return (
    <>
      <Button type="primary" onClick={showModal}>
        <EditOutlined />
      </Button>
      <Modal title="Basic Modal" visible={isModalVisible} onOk={handleOk} onCancel={handleCancel}>
        <p>Pas de projectegevens aan:</p>
        <label for='project-number'>Projectnummer:</label>
        <Input name='project-number' placeholder={project.project_number} defaultValue={project.project_number} disabled />

        <label for='client'>Klant:</label>
        <Input name='client' placeholder={project.client} defaultValue={project.client} onChange={(e) => setClientValue(e.target.value)} />

        <label for='client'>Locatie:</label>
        <Input name='client' placeholder={project.location} defaultValue={project.location} onChange={(e) => setLocationValue(e.target.value)} />

        <label for='project-status'>Status:</label>
        <div className="">
          <Select defaultValue={project.project_status.id} style={{ width: 120 }} onChange={(value) => setStatusValue(value)}>
            {statuses.map((status) =>
              <Option value={status.id}>{status.value}</Option>
            )}
          </Select>
        </div>

        <label for='client'>Kozijnen:</label>
        <Input name='client' placeholder={project.window_frames} defaultValue={project.window_frames} onChange={(e) => setWindowFramesValue(e.target.value)} />

        <label for='doors'>Deuren:</label>
        <Input name='doors' placeholder={project.doors} defaultValue={project.doors} onChange={(e) => setDoorsValue(e.target.value)} />

      </Modal>
    </>
  );
};

export default EditProject;