import React from 'react';
import { Button } from 'antd';
import { DeleteOutlined } from '@ant-design/icons';

const DeleteStatus = ({ status, submitHandler }) => {
  const deleteStatus = () => {
    fetch(`https://broporte-backend.herokuapp.com//api/project-status/${status.id}`, { method: 'DELETE' })
      .then(res => res.json())
      .then(json => {
        submitHandler();
      })
  };


  return (
    <>
      <Button type="primary" onClick={deleteStatus} danger>
        <DeleteOutlined />
      </Button>
    </>
  );
};

export default DeleteStatus;